function launch(el) {
    let scrollActiveLink = window.location.hash ? window.location.hash : '';
    /*
    // Create the observer helper div and appended to the DOM
    */
    const observerHelper = document.createElement('div');
    let stickNavHeight = el.offsetHeight;
    let lastScrollPos = window.scrollY;
    observerHelper.id = 'nav-sticky__helper';
    el.parentNode.insertBefore(observerHelper, el);
    /*
    // Observer to watching menu position and make it stick
    */

    function createObserverStickyNav(helperElStickyNav) {
        let observer;

        observer = new IntersectionObserver(handleIntersect_stickyMenu);
        observer.observe(helperElStickyNav);
    }

    function handleIntersect_stickyMenu(entries) {
        entries.forEach(entry => {
            el.classList.toggle(
                'nav-sticky__inner--stick',
                !entry.isIntersecting
            );
        });
    }

    createObserverStickyNav(observerHelper);
    if (location.hash) {
        //Needed to solve firefox issue, when a page is first loaded it doesn't scroll to position. Not an issue if page is refreshed.
        setTimeout(() => {
            var elId = location.hash.replace('#', '');
            var scrollToEl = document.getElementById(elId);
            scrollToEl.scrollIntoView(true);
        }, 700);
    }

    /*
    // Observer to watching different content sections and highlight corresponding menu item
    */

    const contentSections = document.querySelectorAll('.sticky-nav__sec');

    function handleIntersect_contentSections(entries) {
        let scrollTop = window.scrollY;
        entries.forEach(entry => {
            if (scrollTop >= lastScrollPos) {
                if (entry.isIntersecting) {
                    highlightNavMenuItem(entry.target);
                }
            } else {
                if (!entry.isIntersecting) {
                    highlightNavMenuItem(entry.target.previousElementSibling);
                }
            }
        });
        lastScrollPos = scrollTop;
    }

    function createObserverContentSections() {
        const options = {
            root: null,
            threshold: 0,
            rootMargin: `-${stickNavHeight}px 0px -60% 0px`,
        };

        const observerContentSections = new IntersectionObserver(
            handleIntersect_contentSections,
            options
        );

        contentSections.forEach(area => {
            observerContentSections.observe(area);
        });
    }

    createObserverContentSections();

    /*
    // Observer to watchinging 1st menu item then add blur effect to start of nav
    */

    const stickyNavFirstItem = document.querySelector(
        '.nav-sticky__item:first-child'
    );

    const observeStickyNavFirstItem = new IntersectionObserver(entries => {
        const horizontalScrollWidth = document.querySelector(
            '.nav-sticky__items'
        ).scrollWidth;
        const stickyNavWidth = document.querySelector('.nav-sticky__items')
            .offsetWidth;

        if (stickyNavWidth < horizontalScrollWidth) {
            entries.forEach(entry => {
                document
                    .querySelector('.nav-sticky')
                    .classList.toggle(
                        'nav-sticky--left',
                        !entry.isIntersecting
                    );
            });
        }
    });

    observeStickyNavFirstItem.observe(stickyNavFirstItem);

    /*
    // Observer to watchinging last menu item then add blur effect to end of nav
    */

    const stickyNavLastItem = document.querySelector(
        '.nav-sticky__item:last-child'
    );

    const observeStickyNavLastItem = new IntersectionObserver(entries => {
        const horizontalScrollWidth = document.querySelector(
            '.nav-sticky__items'
        ).scrollWidth;
        const stickyNavWidth = document.querySelector('.nav-sticky__items')
            .offsetWidth;

        if (stickyNavWidth < horizontalScrollWidth) {
            entries.forEach(entry => {
                document
                    .querySelector('.nav-sticky')
                    .classList.toggle(
                        'nav-sticky--right',
                        !entry.isIntersecting
                    );
            });
        }
    });

    observeStickyNavLastItem.observe(stickyNavLastItem);

    /*
    // Functions below, ensuring the corresponding menu item is set to active that corelates 
    // to the section of the page you are viewing. The 'active' menu item is triggered by either
    // clicking on it, or scrolling to a new section
    */

    const stickyNavMenuItemLinks = document
        .querySelector('.nav-sticky')
        .querySelectorAll('.nav-sticky__item__link');
    const anchorLinksOnPage = document.querySelectorAll('a[href^="#"]');

    function highlightNavMenuItem(elem) {
        stickyNavMenuItemLinks.forEach(item => {
            if (
                item.getAttribute('href') === '#' + elem.id &&
                !scrollActiveLink
            ) {
                item.classList.add('nav-sticky__item__link__active');
                scrollNavItemToView(item);
            } else if (
                item.getAttribute('href') === '#' + elem.id &&
                scrollActiveLink === '#' + elem.id
            ) {
                setTimeout(() => {
                    item.classList.add('nav-sticky__item__link__active');
                    scrollActiveLink = ''; // clears value to indicate the page has now scrolled down to the clicked link
                    scrollNavItemToView(item);
                }, '300');
            } else {
                item.classList.remove('nav-sticky__item__link__active');
            }
        });
    }

    function scrollNavItemToView(item) {
        const stickyNavItems = document.querySelector('.nav-sticky__items');
        const stickyNavItemsWidth = stickyNavItems.offsetWidth;
        stickyNavItems.scrollLeft =
            item.offsetLeft - stickyNavItemsWidth / 2 + item.offsetWidth / 2;
    }
    window.addEventListener('hashchange', () => {
        scrollActiveLink = window.location.hash;
    });

    anchorLinksOnPage.forEach(item => {
        item.addEventListener('click', () => {
            // If clicked hash is already in the url remove so it gets added again to trigger the hashchange listener
            const hrefValue = item.getAttribute('href');
            if (hrefValue === window.location.hash) {
                window.location.hash = '';
            }
        });
    });
}

const className = 'nav-sticky__wrap';

export default {
    launch,
    className,
};
