import {
    current as globalCurrent,
    tree as globalTree,
    currentSH as globalCurrentSH,
    treeSH as globalTreeSH,
    currentURL
} from '../nav-mobile/parse-nav';

function processTree(gCurrent, gTree) {

    if (!currentURL.includes('bayes.city.ac.uk/staff-hub')&& gCurrent.length < 3) {
        return { tree: null, current: null };  // left hand navigation only starts on level 3
    } else if  (currentURL.includes('bayes.city.ac.uk/staff-hub') && gCurrent.length < 2) {
        return { tree: null, current: null };  // left hand navigation only starts on level 2 if Staffhub
    }

    const tree = gCurrent
        .slice(1, 3)
        .reduce(
            (acc, currentItem) =>
                acc.children.find(child => child.url === currentItem.url),
            gTree
        );
    const current = gCurrent.slice(2);

    return { current, tree };
}

const { tree, current } = processTree(globalCurrent, globalTree);
const { tree: treeSH, current: currentSH } = processTree(globalCurrentSH, globalTreeSH);

export { tree, current, treeSH, currentSH, currentURL };
